.search-bar {
  height: auto;
  width: 75vw;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 6vw;
  right: 5vw;
}

#search-form {
  width: 60vw;
  height: 1.8vw;
  background-color: #fff;
  border-radius: 3vw;
  align-items: center;
  padding: .5vw;
  display: flex;
}

#city-input {
  width: 1vw;
  height: 1.5vw;
  border: none;
  border-radius: 3vw;
  outline: none;
  flex: 1;
  padding: .5em;
  font-size: 1.2vw;
}

#location-icon, #star-icon {
  width: 2vw;
  fill: #999;
  cursor: pointer;
  font-size: 1.5vw;
}

#location-icon:hover, #star-icon:hover {
  fill: #666;
}

.position-favorite-elemet {
  margin-top: 1vw;
  display: flex;
}

#favorites-list {
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

#favorites-list li {
  color: #fff;
  text-align: center;
  cursor: pointer;
  white-space: nowrap;
  background-color: rgba(16, 33, 54, .5);
  border-radius: 1.2vw;
  margin: 0 .5vw 0 0;
  padding: .5vw;
  font-size: 1.5vw;
  display: inline-block;
}

.remove-favorite {
  cursor: pointer;
  color: rgba(255, 255, 255, .5);
  margin-left: .3vw;
  font-size: 1.6vw;
  transition: color .3s;
}

.remove-favorite:hover {
  color: #c00;
}

#show-more-btn, #show-less-btn {
  text-align: center;
  width: 2vw;
  height: 3vw;
  margin-top: 1.3w;
  color: #fff;
  cursor: pointer;
  background-color: rgba(16, 33, 54, .5);
  border: none;
  border-radius: 8vw;
  padding: 0;
  font-size: .7vw;
}

#show-more-btn:hover, #show-less-btn:hover {
  background-color: #929292;
}

.show-more-icon:before {
  content: "";
  font-family: FontAwesome;
}

.show-less-icon:before {
  content: "";
  font-family: FontAwesome;
}

@media screen and (max-width: 500px) {
  .search-bar {
    width: 90vw;
  }

  #search-form {
    width: 70vw;
    height: auto;
    background-color: #fff;
    border-radius: 3vw;
    align-items: center;
    padding: .5vw;
    display: flex;
  }

  #city-input {
    width: 1vw;
    height: 2.5vw;
    font-size: 1.8vw;
  }

  #location-icon, #star-icon {
    width: 4vw;
    font-size: 2.5vw;
  }

  #favorites-list {
    margin-top: 3vw;
  }

  #favorites-list li {
    color: #fff;
    cursor: pointer;
    white-space: nowrap;
    background-color: rgba(16, 33, 54, .5);
    border-radius: 1.5vw;
    margin: 0 1.5vw .5vw 0;
    padding: .5vw;
    font-size: 3.2vw;
    display: inline-block;
  }

  .remove-favorite {
    cursor: pointer;
    color: rgba(255, 255, 255, .5);
    padding-left: 1.3vw;
    font-size: 2.6vw;
    transition: color .3s;
  }

  #show-more-btn, #show-less-btn {
    text-align: center;
    width: 5vw;
    height: 5vw;
    color: #fff;
    cursor: pointer;
    background-color: rgba(84, 85, 86, .5);
    border: none;
    border-radius: 8vw;
    margin-top: 3vw;
    margin-left: 5vw;
    padding: 0;
    font-size: 2vw;
  }
}

@keyframes slideOpen {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

#search-form {
  width: 60vw;
  height: 1.8vw;
  background-color: #fff;
  border-radius: 3vw;
  align-items: center;
  padding: .5vw;
  display: flex;
  overflow: hidden;
}

#search-form.slide-open {
  width: 0;
  margin-left: auto;
  margin-right: auto;
  animation: slideOpen 1s forwards;
}

#city-input {
  width: 100%;
  height: 1.5vw;
  border: none;
  border-radius: 3vw;
  outline: none;
  flex: 1;
  padding: .5em;
  font-size: 1.2vw;
}

.master-weather-card {
  width: 35vw;
  height: 25vw;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  top: 10vw;
  right: 0;
  overflow-x: hidden;
  display: flex !important;
}

.weather-card {
  width: 32vw;
  height: 20vw;
  background-color: #102136;
  border-radius: 3vw 0 0 3vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 .3vw .5vw rgba(16, 33, 54, .59);
}

.city-name {
  color: rgba(255, 255, 255, .61);
  justify-content: center;
  margin-bottom: 0;
  font-size: 1.2vw;
  line-height: 1.375;
  display: flex;
}

.temperature-card {
  font-family: var(--font-family);
  font-weight: 1vw;
  letter-spacing: .05vw;
  text-align: center;
  color: #fff;
  margin: 0;
  font-size: 6vw;
}

.divide-line {
  margin: 0 1vw;
}

.description {
  justify-content: center;
  margin-top: 1vw;
  font-size: 1vw;
  display: flex;
}

.description img {
  width: 2vw;
}

.min-temp, .max-temp, .humidity {
  color: rgba(255, 255, 255, .3);
  justify-content: center;
  display: flex;
}

.min-temp, .max-temp {
  color: #fcfcfc;
  margin-bottom: 0;
  font-size: 1.5vw;
}

.min-text, .max-text {
  color: rgba(255, 255, 255, .3);
  flex-direction: column;
  align-items: center;
  font-size: 1.2vw;
  display: flex;
}

.min, .max {
  padding: 0 1vw;
  display: flex;
}

.min {
  border-right: .1vw solid rgba(255, 255, 255, .3);
}

.min-max-container {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.divide-line {
  height: 3vw;
}

.temps-divide {
  justify-content: center;
  display: flex;
}

.button-container {
  flex-direction: row;
  position: absolute;
  top: 21vw;
  display: flex !important;
}

.today-button {
  margin-right: 1vw;
}

.five-day-button {
  margin-right: 0;
}

.weather-button {
  text-transform: uppercase;
  width: 15vw;
  height: 2vw;
  font-family: var(--font-family);
  text-transform: uppercase;
  text-align: center;
  color: #000;
  background-color: rgba(255, 255, 255, .506);
  border: none;
  border-radius: 3vw;
  margin-top: 0;
  padding: 0;
  font-size: 1.4vw;
  font-weight: 700;
}

.weather-button:focus {
  background-color: #fff;
}

#forecast-location {
  font-family: var(--font-family);
  letter-spacing: .04em;
  text-align: center;
  color: #fff;
  font-size: 2vw;
  font-weight: 700;
}

#forecast-container {
  width: 90vw;
  height: auto;
  height: 20vw;
  background: rgba(16, 33, 54, .8);
  border-radius: 3vw;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  padding: 1vw;
  box-shadow: 0 1vw 2vw rgba(16, 33, 54, .6);
}

.five-days-container {
  width: 90vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 10vh;
  right: 5vw;
}

.forecast-day {
  box-sizing: border-box;
  text-align: center;
  flex: 0 18%;
  margin: .1vw;
}

.forecast-day h3, .forecast-day p {
  margin: .2vw 0;
}

.forecast-day, .date-week {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  font-size: 2vw;
  font-weight: 400;
}

.forecast-day .date {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  font-size: 1.5vw;
  font-weight: 400;
}

.forecast-day p.date {
  color: #fff;
  opacity: .55;
  font-family: var(--font-family) !important;
  text-align: center !important;
  font-size: 2vw !important;
  font-weight: 400 !important;
}

.forecast-day .temp {
  font-size: 2vw;
}

.forecast-day .temp-min {
  border-right: .1vw solid rgba(255, 255, 255, .3);
  padding-right: 10px;
}

.forecast-day .temp-max {
  padding-left: 10px;
}

.forecast-day .temp-min, .forecast-day .temp-max {
  font-family: var(--font-family);
  text-align: center;
  color: #ff6b09;
  flex-direction: column;
  font-size: 2vw;
  font-weight: 700;
  display: flex;
}

.forecast-day img {
  width: 3vw;
}

.five-days-forecast {
  justify-content: center;
  display: flex;
}

.five-days-min, .five-days-max {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  opacity: .55;
  font-size: 1.3vw;
  font-weight: 400;
}

.forecast-day .more-info {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  opacity: .3;
  font-size: 1vw;
  font-weight: 700;
  text-decoration: underline;
}

.buttons-five {
  top: 37px;
  left: 60vw;
}

.hourly {
  font-family: var(--font-family);
  text-align: center;
  color: #fff;
  opacity: .54;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
}

.hourly-temp {
  font-family: var(--font-family);
  text-align: center;
  color: #ff6b09;
  margin: 0;
  font-size: 25px;
  font-weight: 700;
}

.hourly-icon img {
  width: 2vw;
}

.hourly-icon {
  margin: 0;
}

.hourly-pressure, .hourly-humidity, .hourly-wind {
  font-family: var(--font-family);
  color: #fff;
  opacity: .54;
  font-size: 14px;
  font-weight: 400;
}

.hourly-humidity {
  padding-right: 28px;
}

.hourly-pressure {
  background-image: url("005-barometer.d8894335.png");
  background-position: 5px 0;
  background-repeat: no-repeat;
}

.hourly-humidity {
  background-image: url("004-humidity.3ce37cc1.png");
  background-position: 5px 0;
  background-repeat: no-repeat;
}

.hourly-wind {
  background-image: url("003-wind.44c0e224.png");
  background-position: 5px 0;
  background-repeat: no-repeat;
}

.hourly-forecast-card {
  width: 120px;
  height: 235px;
  text-align: center;
  box-sizing: border-box;
  width: 120px;
  height: 218px;
  border: 1px solid rgba(255, 255, 255, .2);
  border-radius: 25px;
  margin: 5px;
  display: inline-block;
}

.hourly-forecast-card:hover {
  background: rgba(255, 255, 255, .1);
}

.detailed-forecast {
  width: 100%;
  white-space: nowrap;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 20px;
  display: flex;
  overflow-x: auto;
}

.detailed-forecast-carousel {
  width: 85vw;
  justify-content: center;
  align-items: center;
  margin-top: 2vw;
  display: flex;
  position: relative;
}

.carousel-container {
  width: 90%;
  overflow: hidden;
}

.carousel-items {
  transition: transform .3s;
  display: flex;
}

.carousel-arrow {
  display: none;
}

.left-arrow {
  background-color: rgba(255, 255, 255, .19);
  border-radius: 50%;
  position: absolute;
  bottom: 50px;
  right: 84vw;
}

.right-arrow {
  background-color: rgba(255, 255, 255, .19);
  border-radius: 50%;
  position: absolute;
  bottom: 50px;
  right: 4vw;
}

