.master-weather-card {
  display: flex !important;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  right: 0;
  top: 10vw;
  overflow-x: hidden;
  width: 35vw;
  height: 25vw;
}
.weather-card {
  width: 32vw;
  height: 20vw;
  position: relative;
  border-radius: 3vw 0 0 3vw;
  background-color: #102136;
  box-shadow: 0 0.3vw 0.5vw 0 rgba(16, 33, 54, 0.59);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.city-name {
  font-size: 1.2vw;
  line-height: 1.222;
  // margin-bottom: 26px;

  color: hsla(0, 0%, 100%, 0.611);
  line-height: 1.375;
  margin-bottom: 0;
  display: flex;
  justify-content: center;
}

.temperature-card {
  font-family: var(--font-family);
  font-weight: 1vw;
  font-size: 6vw;
  letter-spacing: 0.05vw;
  text-align: center;
  color: #fff;
  margin: 0;
}

.divide-line {
  margin: 0 1vw;
}

.description {
  // color: #FFFFFF;
  display: flex;
  justify-content: center;
  margin-top: 1vw;
  font-size: 1vw;
}

.description img {
  width: 2vw;
}

.min-temp,
.max-temp,
.humidity {
  color: hsla(0, 0%, 100%, 0.302);
  display: flex;
  justify-content: center;
}

.min-temp,
.max-temp {
  margin-bottom: 0;
  font-size: 1.5vw;
  color: #fcfcfc;
}

.min-text,
.max-text {
  font-size: 1.2vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: hsla(0, 100%, 100%, 0.302);
}

.min,
.max {
  display: flex;
  padding: 0 1vw;
}

.min {
  border-right: 0.1vw solid hsla(0, 0%, 100%, 0.302);
}

.min-max-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.divide-line {
  height: 3vw;
}

.temps-divide {
  display: flex;
  justify-content: center;
}

.button-container {
  // right: 0;
  position: absolute;
  top: 21vw;
  display: flex !important;
  flex-direction: row;
}

.today-button {
  margin-right: 1vw;
}

.five-day-button {
  margin-right: 0;
}
.weather-button {
  text-transform: uppercase;
  width: 15vw;
  height: 2vw;
  border-radius: 3vw;
  border: none;
  margin-top: 0;
  background-color: #ffffff81;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 1.4vw;
  text-transform: uppercase;
  text-align: center;
  color: #000;
  padding: 0;
}

.weather-button:focus {
  background-color: #ffffff;
}

#forecast-location {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 2vw;
  letter-spacing: 0.04em;
  text-align: center;
  color: #fff;
}

#forecast-container {
  flex-direction: row;
  padding: 2vw;
  background-color: #36454f;
  border-radius: 3vw;
  width: 90vw;
  height: auto;
  box-shadow: 0 1vw 2vw 0 rgba(16, 33, 54, 0.6);
  background: rgba(16, 33, 54, 0.8);
  align-items: center;
  flex-wrap: wrap;
  padding: 1vw;
  height: 20vw;
  justify-content: center;
}

.five-days-container {
  width: 90vw;
  display: flex;
  position: absolute;
  right: 5vw;
  bottom: 10vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.forecast-day {
  flex: 0 1 18%;
  box-sizing: border-box;
  margin: 0.1vw;
  text-align: center;
}

.forecast-day h3,
.forecast-day p {
  margin: 0.2vw 0;
}

.forecast-day,
.date-week {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 2vw;
  text-align: center;
  color: #fff;
}

.forecast-day .date {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 1.5vw;
  text-align: center;
  color: #fff;
}

.forecast-day p.date {
  color: #fff;
  opacity: 0.55;
  font-family: var(--font-family) !important;
  font-weight: 400 !important;
  font-size: 2vw !important;
  text-align: center !important;
}

.forecast-day .temp {
  font-size: 2vw;
}

.forecast-day .temp-min {
  border-right: 0.1vw solid hsla(0, 0%, 100%, 0.302);
  padding-right: 10px;
}

.forecast-day .temp-max {
  padding-left: 10px;
}

.forecast-day .temp-min,
.forecast-day .temp-max {
  font-family: var(--font-family);
  text-align: center;
  color: #ff6b09;
  font-size: 2vw;
  font-weight: 700;
  display: flex;
  flex-direction: column;
}

.forecast-day img {
  width: 3vw;
}

.five-days-forecast {
  display: flex;
  justify-content: center;
}

.five-days-min,
.five-days-max {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 1.3vw;
  text-align: center;
  color: #fff;
  opacity: 0.55;
}

.forecast-day .more-info {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 1vw;
  text-decoration: underline;
  text-align: center;
  color: #fff;
  opacity: 0.3;
}

.buttons-five {
  top: 37px; //modif de la 0 ===========================modif
  left: 60vw;
}

.hourly {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  color: #fff;
  opacity: 0.54;
  margin-bottom: 0;
}

.hourly-temp {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 25px;
  text-align: center;
  color: #ff6b09;
  margin: 0;
}

.hourly-icon img {
  width: 2vw;
}

.hourly-icon {
  margin: 0;
}

.hourly-pressure,
.hourly-humidity,
.hourly-wind {
  font-family: var(--font-family);
font-weight: 400;
font-size: 14px;
color: #fff;
opacity: 0.54;
}

//daor daca vrem la stanga========================================
.hourly-humidity {
padding-right: 28px;
}
//==============================================================

.hourly-pressure {
  background-image: url(../images/005-barometer.png);
  background-repeat: no-repeat;
  background-position: 5px 0;
}

.hourly-humidity {
  background-image: url(../images/004-humidity.png);
  background-repeat: no-repeat;
  background-position: 5px 0;
}

.hourly-wind {
  background-image: url(../images/003-wind.png);
  background-repeat: no-repeat;
  background-position: 5px 0;
}



.hourly-forecast-card {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  width: 120px;
  height: 235px;
  margin: 5px;
  text-align: center;
  box-sizing: border-box;
  display: inline-block;
  width: 120px; 
  height: 218px;
  margin: 5px;
}

.hourly-forecast-card:hover {
  background: rgba(255, 255, 255, 0.1);
}

.detailed-forecast {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 20px;
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
}

/* CSS adițional pentru carusel */
.detailed-forecast-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: 2vw;
  width: 85vw;
}

.carousel-container {
  overflow: hidden;
  width: 90%;
}

.carousel-items {
  display: flex;
  transition: transform 0.3s ease;
}

.carousel-arrow {
  display: none;
}

