.left-arrow {
  position: absolute;
  right: 84vw;
  bottom: 50px;
  border-radius: 50%;
  background-color: #ffffff30;
}

.right-arrow {
  position: absolute;
  right: 4vw;
  bottom: 50px;
  border-radius: 50%;
  background-color: #ffffff30;
}

